<template>
    <div class="container">
      <div class="row mt-5">
        <div class="col-xl-6 col-lg-6 col-md-9 col-sm-11 col-xs-11 m-auto">
          <div v-if="showAlert" class="alert alert-warning alert-dismissible fade show" role="alert">
            <strong>{{ alertMessage }}</strong>
            <button type="button" @click="closeAlert" class="btn-close" aria-label="Close"></button>
          </div>
          <h1 class="text-center">To Do List</h1>
          <form @submit.prevent="handleSubmit">
            <div class="mb-3">
              <label for="title" class="form-label">Title</label>
              <input type="text" class="form-control" id="title" v-model="title">
            </div>
            <div class="mb-3">
              <label for="description" class="form-label">Description</label>
              <textarea class="form-control" id="description" v-model="description"></textarea>
            </div>
            <div class="mb-3 form-check">
              <input type="checkbox" class="form-check-input" id="important" v-model="important">
              <label class="form-check-label" for="important">Important</label>
            </div>
            <button type="submit" class="btn btn-primary">{{ editMode ? 'Update' : 'Submit' }}</button>
          </form>
        </div>
      </div>
      <table class="table table-sm mt-5">
        <thead>
          <tr>
            <th>Title</th>
            <th>Description</th>
            <th>Actions</th>
          </tr>
        </thead>
        <tbody>
          <tr v-for="(task, index) in tasks" :key="index">
            <td>{{ task.title }}</td>
            <td>{{ task.description }}</td>
            <td class="btn-group">
              <button class="btn btn-sm btn-primary" @click="editTask(index)">Edit</button>
              <button class="btn btn-sm btn-danger" @click="deleteTask(index)">Delete</button>
            </td>
          </tr>
        </tbody>
      </table>
    </div>
  </template>
  
  <script>
  export default {
    data() {
      return {
        title: '',
        description: '',
        important: false,
        editMode: false,
        editIndex: null,
        showAlert: false,
        alertMessage: '',
        tasks: []
      };
    },
    created() {
      this.loadTasks();
    },
    methods: {
      loadTasks() {
        this.tasks = JSON.parse(localStorage.getItem('tasks')) || [];
      },
      handleSubmit() {
        if (this.title.trim() === '') {
          this.alertMessage = 'Title cannot be empty.';
          this.showAlert = true;
          return;
        }
  
        if (this.editMode) {
          // Update existing task
          this.tasks[this.editIndex] = {
            title: this.title,
            description: this.description,
            important: this.important
          };
          this.alertMessage = 'Task updated successfully.';
        } else {
          // Add new task
          const task = {
            title: this.title,
            description: this.description,
            important: this.important
          };
          this.tasks.push(task);
          this.alertMessage = 'Task added successfully.';
        }
  
        localStorage.setItem('tasks', JSON.stringify(this.tasks));
        this.loadTasks();
        this.resetForm();
        this.showAlert = true;
      },
      editTask(index) {
        const task = this.tasks[index];
        this.title = task.title;
        this.description = task.description;
        this.important = task.important;
        this.editIndex = index;
        this.editMode = true;
      },
      deleteTask(index) {
        this.tasks.splice(index, 1);
        localStorage.setItem('tasks', JSON.stringify(this.tasks));
        this.alertMessage = 'Task deleted successfully.';
        this.showAlert = true;
      },
      resetForm() {
        this.title = '';
        this.description = '';
        this.important = false;
        this.editMode = false;
        this.editIndex = null;
      },
      closeAlert() {
        this.showAlert = false;
      }
    }
  };
  </script>
  
  <style>
  </style>
  